
body, html {
    margin: 0;
    padding: 0;
}
.container {
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-justify-content: center;
        -ms-flex-pack: center;
            justify-content: center;
}
.bg {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    z-index: -1;
    background: #03165a;
}
.main {
    position: absolute;
    background-color:rgba(0,14,69,.6);
    width: 10%;
    height: 300px;
    left : 5%;
    top:5%;
    padding:5px;
}
button {
    padding:  5px 2px;
    width : 80px;
    border:1px solid #ccc;
}



